// ** React Imports
import { Fragment, useState, useEffect, forwardRef } from 'react'

import Avatar from '@components/avatar'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
// ** Store & Actions
import { getData, deleteCategory } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import { Can } from '@src/utility/context/Can.js'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { Edit, Trash } from 'react-feather'

import CategoryAdd from '../add/AddCard'
import CategoryEdit from '../edit/EditCard'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Input,
  Button,
  Table
} from 'reactstrap'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
const MySwal = withReactContent(Swal)
// ** Table Header
const CustomHeader = ({ handleFilter, searchTerm, rowsPerPage = 10 }) => {
  // ** Converts table to CSV
  const [addCategoryFlag, setAddCategoryFlag] = useState(false)

  // ** Renders Client Columns

  const handleCallback = (() => {
    setAddCategoryFlag(false)
  })

  return (
    <div className='invoice-list-table-header w-100 me-1 ms-50 mb-75'>
      <Row className='p-1'>
        <Col xl='6' className='d-flex align-items-center p-1'>
          <div className='d-flex align-items-center w-100'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              className='mx-50'
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              style={{ width: '5rem' }}
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
            <label htmlFor='rows-per-page'>Entries</label>
            {/* 
            {selectedIds.length > 0 && (
              <div>
                <Button.Ripple className='btn-icon rounded-circle ms-1' color='danger' id="delete_btn">
                  <Trash size={16} />
                </Button.Ripple>
                <UncontrolledTooltip target='delete_btn'>Delete</UncontrolledTooltip>
              </div>
            )} */}
          </div>
        </Col>
        <Col md='6' sm='6'
          xl='6'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <div className='d-flex align-items-center mb-sm-0 mb-1 me-1'>
            <label className='mb-0' htmlFor='search-invoice'>
              Search:
            </label>
            <Input
              id='search-invoice'
              className='ms-50 w-100'
              type='text'
              value={searchTerm}
              onChange={e => handleFilter(e.target.value)}
            />
          </div>

          <div className='d-flex align-items-center table-header-actions'>
            <Can I={'write'} a={'category'}>
              <Button className='add-new-user' color='primary' onClick={() => { setAddCategoryFlag(!addCategoryFlag) }}>
                Add Category
              </Button>
            </Can>
          </div>
        </Col>
      </Row>
      <CategoryAdd addCategoryFlag={addCategoryFlag} parentCallback={handleCallback} />
    </div>
  )
}

const CategoriesList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const store = useSelector(state => state.category)
  const rowsPerPage = 10
  const sortColumn = 'createdon'
  const [searchTerm, setSearchTerm] = useState('')
  const [currentPage, setCurrentPage] = useState(1)
  const [categoryEdit, setcategoryEdit] = useState(null)
  const [editCategoryFlag, setEditCategoryFlag] = useState(false)

  const renderCategory = row => {
    return (<Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.name}
    />)
  }

  const editCategory = (val) => {
    setcategoryEdit(val)
    setEditCategoryFlag(!editCategoryFlag)
  }

  const deleteCategoryfun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteCategory(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Category has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }


  // ** States
  const sort = 'desc'

  // ** Bootstrap Checkbox Component
  const BootstrapCheckbox = forwardRef((props, ref) => (
    <div className='form-check'>
      <Input type='checkbox' ref={ref} {...props} />
    </div>
  ))

  // ** Get data on mount
  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: searchTerm,
        perPage: rowsPerPage,
        page: currentPage
      })
    )
    setBlock(false)
  }, [dispatch, store.data.length])

  // ** Function in get data on search query change
  const handleFilter = async val => {
    setSearchTerm(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn,
        q: val,
        perPage: rowsPerPage,
        page: currentPage
      })
    )
    setBlock(false)
  }


  // ** Function in get data on page change
  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: searchTerm,
        sortColumn,
        page: page.selected + 1,
        perPage: rowsPerPage
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName='active'
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={page => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return []
    }
  }

  useEffect(() => {
    dataToRender()
  }, [store.data])

  const handleCallback = (() => {
    setEditCategoryFlag(false)
    setcategoryEdit(null)
  })

  return (
    <Fragment>
      <CategoryEdit editCategoryFlag={editCategoryFlag} parentCallback={handleCallback} category={categoryEdit} />
      <UILoader blocking={block} loader={<Loader />}>
        <Card className='overflow-hidden'>
          <div className='react-dataTable'>
            <CustomHeader handleFilter={handleFilter} searchTerm={searchTerm} />
            <Table className='table-hover'>
              <thead>
                <tr>
                  <th>Category</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().map((category, k) => {
                  return (
                    <tr key={k}>
                      <td>
                        {renderCategory(category)} <span>{category.name}</span>
                      </td>
                      <td>
                        <Can I={'write'} a={'category'}>
                          <Edit onClick={() => editCategory(category)}
                            className='cursor-pointer ms-1 mt-0' size={16} />
                        </Can>

                        <Can I={'delete'} a={'category'}>
                          <Trash onClick={e => {
                            e.preventDefault()
                            deleteCategoryfun(category.id)
                          }}
                            className='cursor-pointer ms-1 mt-0' size={16} />
                        </Can>
                      </td>
                    </tr>
                  )
                })
                }
              </tbody>
            </Table>

            <CustomPagination />
          </div>
        </Card>
      </UILoader>
    </Fragment>
  )
}

export default CategoriesList
